import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader, Upload, Icon } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const { TextArea } = Input;
import BenefitsItem from '../../../components/FormItem/benefitsItem.jsx'

export class StockForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            "assetsType": {
                "debts":[],
                "stock": [],
                "assets": []
            },
            "industryList": [],
            "cascaderRegion": [],
            "project": {   //项目
                "key": "", // 编号
                "title": "", //标题
                "category": "", //类别
                "type": "", //类型
                "detail": "",   //详情
                "accessory": {        //附件
                    "name": "",
                    "link": ""
                }
            },
            "financing": {  //融资方
                "industry": "", //行业
                "aptitude": "", //资质
                "parentAptitued": "", //母公司资质
                "region": [], //地区
                "previousProfits": "", //去年净利润
                "recentProfits": "",
                "nextProfits": "",
                "number": "", //金额
                "deadline": "", //期限
                "maxRate": "" //最高可接受利率
            },
            "mortgage": {       //抵押物
                "isNeed": "",    //是否有抵押物
                "type": [],  //抵押物类型
                "name": "",  //抵押物名称
                "value": "",  //抵押物价值
                "rate": "" //抵押物折扣率
            },
            "guarantee": {
                "isNeed": "",    //是否必须担保
                "method": ""   //担保方式
            },
            "stock": {
                "type": "",  //参股性:1   控股型:2
                "property": "",  //老股转让:1   新股增发:2
                "number": "",  //转让金额
                "rate": "", //出让股权比例
                "estimate": {  //估值
                    "times": "",
                    "type": ""
                }
            },
            "target": {  //标的企业
                "name": "",
                "industry": "",
                "region": "",
                "value": "", //企业整体估值
                "previousProfits": "",
                "recentProfits": "",
                "nextProfits": ""
            },
            "assets": { //资产
                "region": "",
                "number": "",
                "value": ""
            },
            'fileList': []
        };
        ['publish', 'save', 'resetForm', 'stockPropertyChange', 'uploadChange'].forEach(m=>this[m] = this[m].bind(this))
    }

    stockPropertyChange(val) {
        Object.assign(this.state.stock, {property: val});
        this.setState({
            stock: this.state.stock
        })
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        ajax({
            url: "/project/publish/getInfoDetail",
            method: "post",
            data: {id: that.props.infoID ? that.props.infoID : ""},
            callback: function (data) {
                let newData = data;
                that.setState(Object.assign(that.state, newData));
                that.setState({
                    fileList: [{
                        uid: "random",
                        name: newData.accessory.name,
                        url: newData.accessory.link,
                        status: "done",
                        response: 'Server Error 500'
                    }]
                })
            }
        })
    }

    publish(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                ajax({
                    url: "/project/publish/doPublish",
                    data: Object.assign(values, {id: this.props.infoID}, {accessory: {name: this.state.accessory.name, link: this.state.accessory.link}}),
                    callback: function(data) {
                        message.success("发布成功!", 1, function() {
                            hashHistory.push("/project/space/published")
                        });
                    }
                });
            }
        })
    }

    save(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                ajax({
                    url: "/project/publish/saveDraft",
                    data: Object.assign(values, {id: this.props.infoID}, {accessory: {name: this.state.accessory.name, link: this.state.accessory.link}}),
                    callback: function(data) {
                        message.success("保存成功!", 1, function() {
                            hashHistory.push("/project/space/draft")
                        });
                    }
                });
            }
        })
    }

    resetForm() {
        this.props.form.resetFields();
    }

    uploadChange(info) {
        let that = this;
        let file = info.file;
        let fileList = info.fileList;
        this.setState({loading: true});
        if(fileList.length > 0) {
            if(fileList.length > 1) {
                fileList = [file];
            }
            if(fileList[0].status == "error") {
                fileList = [];
                message.error("上传图片失败!");
            }
            if(fileList[0].status == "done") {
                fileList[0].url = `${fileList[0].response.url}`;
                this.setState({
                    accessory: {
                        name: fileList[0].name,
                        link: fileList[0].url
                    }
                })
            }
        } else {
            this.setState({
                accessory: {
                    name: "",
                    link: ""
                }
            })
        }

        this.setState({fileList, loading: false});
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 5 },
            wrapperCol: { span: 14 }
        };
        return (
            <Form>
                <FormItem
                    required
                    {...formItemLayout}
                    label="项目标题"
                    hasFeedback
                >
                    {getFieldDecorator("project.title", {
                        rules: [{required: true, message: "请输入标题!"}],
                        initialValue: this.state.project.title
                    })(
                        <Input placeholder="请输入标题" />
                    )}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="项目类别"
                    hasFeedback
                >
                    {getFieldDecorator("project.category", {
                        initialValue: "2"
                    })(
                        <Select disabled>
                            <Option value="1">债权</Option>
                            <Option value="2">股权</Option>
                            <Option value="4">资产转让</Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="参/控股"
                    hasFeedback
                >
                    {getFieldDecorator("stock.type", {
                        rules: [{
                            required: true,
                            message: "请选择类型!"
                        }],
                        initialValue: this.state.stock.type
                    })(
                        <RadioGroup>
                            <Radio value="1">参股型项目</Radio>
                            <Radio value="2">控股型项目</Radio>
                        </RadioGroup>
                    )}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="项目类型"
                    hasFeedback
                >
                    {getFieldDecorator("project.type", {
                        rules: [{
                            required: true,
                            message: "请选择项目类型!"
                        }],
                        initialValue: this.state.project.type
                    })(
                        <Select>
                            {
                                this.state.assetsType.stock.map(function(item) {
                                    return <Option value={item.key} key={item.key}>{item.value}</Option>
                                })
                            }
                        </Select>
                    )}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="股权性质"
                    hasFeedback
                >
                    {getFieldDecorator("stock.property", {
                        rules: [{
                            required: true,
                            message: "请选择股权性质"
                        }],
                        initialValue: this.state.stock.property
                    })(
                        <Select onChange={this.stockPropertyChange}>
                            <Option value="1">老股转让</Option>
                            <Option value="2">新股增发</Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label={this.state.stock.property == "1" ? "转让方名称" : "融资方名称"}
                >
                    <div>详见我的联系方式</div>
                </FormItem>
                {
                    this.state.stock.property == "1" ?
                        <FormItem
                            required
                            {...formItemLayout}
                            label= "标的企业名称"
                            hasFeedback
                        >
                            {getFieldDecorator("target.name", {
                                rules: [{
                                    required: true,
                                    message: "请输入标的企业名称!"
                                }],
                                initialValue: this.state.target.name
                            })(
                                <Input placeholder="请输入标的企业名称" />
                            )}
                        </FormItem> : ""
                }
                <FormItem
                    required
                    {...formItemLayout}
                    label={this.state.stock.property == "1" ? "标的企业所属行业" : "融资方所属行业"}
                    hasFeedback
                >
                    {getFieldDecorator(this.state.stock.property == "1" ? "target.industry" : "financing.industry", {
                        rules: [{
                            required: true,
                            message: "请设置所属行业!"
                        }],
                        initialValue: this.state.stock.property == "1" ? this.state.target.industry : this.state.financing.industry
                    })(
                        <Select>
                            {
                                this.state.industryList.map(function(item) {
                                    return <Option value={item.key} key={item.key}>{item.value}</Option>
                                })
                            }
                        </Select>
                    )}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label={this.state.stock.property == "1" ? "标的企业所在地" : "融资方所在地"}
                    hasFeedback
                >
                    {getFieldDecorator(this.state.stock.property == "1" ? "target.region" : "financing.region", {
                        rules: [{
                            required: true,
                            message: "请设置所在地!"
                        }],
                        initialValue: this.state.stock.property == "1" ? this.state.target.region : this.state.financing.region
                    })(
                        <Cascader options={this.state.cascaderRegion} />
                    )}
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label={this.state.stock.property == "1" ? "标的企业去年利润" : "融资方去年利润"}
                    hasFeedback
                >
                    {getFieldDecorator(this.state.stock.property == "1" ? "target.previousProfits" : "financing.previousProfits", {
                        rules: [{
                            required: true,
                            message: '请输入去年净利润!'
                        }],
                        initialValue:this.state.stock.property == "1" ? this.state.target.previousProfits : this.state.financing.previousProfits
                    })(
                        <Input placeholder="请输入去年净利润" style={{width: "85%"}} />
                    )}
                    <span className="ant-form-text" style={{marginLeft: 7}}>万元</span>
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label={this.state.stock.property == "1" ? "转让金额" : "融资金额"}
                    hasFeedback
                >
                    {getFieldDecorator("stock.number", {
                        rules: [{
                            required: true,
                            message: "请输入金额!"
                        }],
                        initialValue: this.state.stock.number
                    })(
                        <Input placeholder="请输入金额" style={{width: "85%"}} />
                    )}
                    <span className="ant-form-text" style={{marginLeft: 7}}>万元</span>
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="出让股权比例"
                >
                    {getFieldDecorator("stock.rate", {
                        initialValue: this.state.stock.rate
                    })(
                        <Input placeholder="出让股权比例" style={{width: "85%"}} />
                    )}
                    <span className="ant-form-text" style={{marginLeft: 7}}>%</span>
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="企业整体估值"
                >
                    {getFieldDecorator(this.state.stock.property == "1" ? "target.number" : "financing.number", {
                        initialValue: this.state.stock.property == "1" ? this.state.target.number : this.state.financing.number
                    })(
                        <Input placeholder="企业整体估值" style={{width: "85%"}} />
                    )}
                    <span className="ant-form-text" style={{marginLeft: 7}}>万元</span>
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="投资估值倍数"
                >
                    {getFieldDecorator("stock.estimate", {
                        initialValue: {
                            times: this.state.stock.estimate.times,
                            type: this.state.stock.estimate.type
                        }
                    })(<BenefitsItem />)}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="企业今年预计净利润"
                >
                    {getFieldDecorator(this.state.stock.property == "1" ? "target.recentProfits" : "financing.recentProfits", {
                        initialValue: this.state.stock.property == "1" ? this.state.target.recentProfits : this.state.financing.recentProfits
                    })(
                        <Input placeholder="请输入企业今年预计净利润" style={{width: "85%"}} />
                    )}
                    <span className="ant-form-text" style={{marginLeft: 7}}>万元</span>
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="企业明年预计净利润"
                >
                    {getFieldDecorator(this.state.stock.property == "1" ? "target.nextProfits" : "financing.nextProfits", {
                        initialValue: this.state.stock.property == "1" ? this.state.target.nextProfits: this.state.financing.nextProfits
                    })(
                        <Input placeholder="请输入企业明年预计净利润" style={{width: "85%"}} />
                    )}
                    <span className="ant-form-text" style={{marginLeft: 7}}>万元</span>
                </FormItem>
                <FormItem
                    required
                    {...formItemLayout}
                    label="项目详情"
                    hasFeedback
                >
                    {getFieldDecorator("project.detail", {
                        rules: [{required: true, message: "请输入资金详情!"}],
                        initialValue: this.state.project.detail
                    })(
                        <TextArea placeholder="请输入项目详情" autosize={{ minRows: 8 }} />
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="附件"
                >
                    <Upload
                        name="file"
                        action="/v1/upload"
                        fileList={this.state.fileList}
                        onChange={this.uploadChange}
                    >
                        <Button>
                            <Icon type="upload" />上传文件
                        </Button>
                    </Upload>
                </FormItem>
                <FormItem wrapperCol={{ span: 16, offset: 3 }}>
                    <Button type="primary" onClick={this.publish} style={{"marginRight": "20px"}}>立即发布</Button>
                    <Button type="danger" onClick={this.resetForm} style={{"marginRight": "20px"}}>重置</Button>
                    <Button onClick={this.save}>保存</Button>
                </FormItem>
            </Form>
        )
    }
}

const WrappedStockForm = Form.create({})(StockForm);

module.exports = WrappedStockForm;


